import React, { Component, lazy, Suspense } from "react";
import { connect } from "react-redux";
import { Field, reduxForm, reset } from "redux-form";
import SelectSearch from "react-select-search";
import { getUUID } from "../actions/fakturAction";
import Swal from "sweetalert2";
import { getListTransaksiPenjualan } from "../actions/transaksiPenjualanAction";
import {
  finishSend,
  getBruto,
  getGross,
  getK,
  getNetto,
  getP,
  getPkg,
  getQty,
  getRp,
  progressSend,
} from "../actions/LogicAction";
import PrintNota from "../Stocking/Component/PrintNota";
import { gagal, warning } from "../Alert";
import ReactTooltip from "react-tooltip";
import { getTotalNW } from "../actions/returnPenjualanAction";
import Skeleton from "react-loading-skeleton";
import { showModal } from "../actions/ModalAction";
import HeaderCard from "../HeaderCard";
import { getLokasi } from "../actions/lokasiAction";
import { getsalesmanList } from "../actions/salesmanAction";
import { getcustomerList } from "../actions/customerAction";
import { AxiosMasterGet, AxiosMasterPost } from "../Axios";
import GlobalTabel from "../GlobalComponent/GlobalTabel";
import FormBarcodePenjualanCustomer from "./Component/FormBarcodePenjualanCustomer";
import FormTimbanganPenjualanCustomer from "./Component/FormTimbanganPenjualanCustomer";
import FormPOCustomer from "./Component/FormPOCustomer";
import { getListPOPenjualan } from "../actions/validasiPOAction";
import FormPackingCustomer from "./Component/FormPackingCustomer";
import { renderField, renderFieldSelect } from "../renderField";

const ModalMasterData = lazy(() =>
  import("../DataMaster/Component/ModalMasterData")
);
const FormTransaksiPenjualan = lazy(() =>
  import("./Component/FormTranskasiPenjualan")
);
const maptostate = (state) => {
  return {
    noFaktur: state.provinsi.noFaktur,
    transaksiPenjualan: state.provinsi.transaksiPenjualan,
    listCustomer: state.provinsi.customer,
    listLokasi: state.provinsi.lokasi,
    listSales: state.provinsi.salesman,
    totalNW: state.provinsi.totalNW,
    sumNetto: state.perhitungan.sumNetto,
    sumBruto: state.perhitungan.sumBruto,
    sumPkg: state.perhitungan.sumPkg,
    sumP: state.perhitungan.sumP,
    sumK: state.perhitungan.sumK,
    sumQty: state.perhitungan.sumNetto,
    sumGross: state.perhitungan.sumGross,
    sumRp: state.perhitungan.sumRp,
    username: state.provinsi.username,
    onSend: state.provinsi.onSend,
    kodeBarang: state.provinsi.kodeBarang,
  };
};
class TransaksiPenjualan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listLokasi: [],
      listSales: [],
      listCustomer: [],
      listPacking: [],
      kodeCustomer: "",
      columns: [
        {
          dataField: "kode_jenis",
          text: "Kode Barang",
        },
        {
          dataField: "kadar_cetak",
          text: "Kadar\nCetak(%)",
        },
        // {
        //   dataField: "plastik",
        //   text: "P",
        // },
        // {
        //   dataField: "kulit",
        //   text: "K",
        // },
        {
          dataField: "pkg",
          text: "Bungkus\n(Gr)",
        },
        {
          dataField: "bruto",
          text: "Berat\n(Gr)",
        },
        {
          dataField: "kadar_jual",
          text: "Harga\n(%)",
        },
        {
          dataField: "netto",
          text: "Murni\n(Gr)",

          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "disc_persen",
          text: "%",

          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "disc",
          text: "Disc\n(Gr)",

          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "netto_disc",
          text: "Netto\nDisc(Gr)",
        },
        {
          dataField: "ongkos_rp",
          text: "Ongkos\n(Rp)",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, rowIndex) => {
            return (
              <div className="text-center">
                <button
                  data-tip
                  data-for="hapus"
                  className="btn btn-danger mr-2"
                  onClick={() => this.deleteRow(rowIndex, row.netto_disc)}
                >
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                  <i className="fa fa-trash mr-1"></i>
                </button>
                <br />
              </div>
            );
          },
        },
      ],
      discGlobalGr: 0,
      discGlobalPr: 0,
      totalNWExt: 0,
      noBon: "",
      namaCustomer: "",
      lokasi: "",
      sales: "",
      namaToko: "",
      tanggal: "",
      data: "",
    };
  }

  deleteRow(rowIndex, dihapus) {
    Swal.fire({
      title: "Anda Yakin ?",
      text: "Anda Akan Menghapus Data Ini ?",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!",
    }).then((result) => {
      if (result.isConfirmed) {
        const array =
          JSON.parse(localStorage.getItem("transaksiPenjualan")) || [];
        array.splice(rowIndex, 1);
        localStorage.setItem("transaksiPenjualan", JSON.stringify(array));
        var awal = localStorage.getItem("GrandTotalNetto");
        var hasil = parseFloat(awal) - parseFloat(dihapus);
        localStorage.setItem("GrandTotalNetto", parseFloat(hasil).toFixed(3));
        Swal.fire({
          text: "Berhasil",
          timer: 2000,
          showConfirmButton: false,
          position: "top-right",
          icon: "success",
        })
          .then(() => this.props.dispatch(getTotalNW()))
          .then(() => this.props.dispatch(getListTransaksiPenjualan()))
          .then(() =>
            this.setState({
              GrandTotalNetto: (
                parseFloat(localStorage.getItem("GrandTotalNetto") || 0) - 0
              ).toFixed(3),
            })
          );
      }
    });
  }
  componentDidMount() {
    localStorage.removeItem("transaksiPenjualan");
    localStorage.removeItem("transaksiPenjualan_level2");
    localStorage.removeItem("transaksiPenjualan_level3");
    localStorage.removeItem("GrandTotalNetto");
    this.props.dispatch(getLokasi());
    this.props.dispatch(getsalesmanList());
    this.props.dispatch(getcustomerList());
    this.props.dispatch(getUUID());
    this.props.dispatch(getListTransaksiPenjualan());
    this.props.dispatch(getListPOPenjualan());
    this.props.dispatch(getTotalNW());
    this.props.dispatch(getBruto("transaksiPenjualan"));
    this.props.dispatch(getNetto("transaksiPenjualan"));
    this.props.dispatch(getPkg("transaksiPenjualan"));
    this.props.dispatch(getRp("transaksiPenjualan"));
    this.props.dispatch(getGross("transaksiPenjualan"));
    this.props.dispatch(getP("transaksiPenjualan"));
    this.props.dispatch(getK("transaksiPenjualan"));
    this.props.dispatch(getQty("transaksiPenjualan"));
    this.setState({
      GrandTotalNetto: (
        parseFloat(localStorage.getItem("GrandTotalNetto") || 0) - 0
      ).toFixed(3),
    });
  }

  handleSubmit(data) {
    let param = data.kodeBarang;
    let hasil = this.props.kodeBarang.filter(
      (data) => data.kode_jenis === param
    );
    AxiosMasterGet(`jenis/get/by-kode-jenis/${data.kodeBarang}`)
      .then((res) => {
        var simpan = {
          netto_disc: parseFloat(data.nettoDiscGlobalExt).toFixed(3),
          gross: parseFloat(data.gross).toFixed(3), //berat + bungkus
          netto: parseFloat(data.netto).toFixed(3), //murni
          qty: 1,
          k: parseFloat(data.k),
          ongkos_rp: parseFloat(data.rp === undefined ? 0 : data.rp), //ongkos
          kadar_cetak: hasil[0].kadar_cetak,
          disc_persen: data.discGlobalPr,
          kadar_jual: parseFloat(data.kadarTransaksi), //harga%
          kadar_level2: res.data.level2,
          kadar_level3: res.data.level3,
          kadar_modal: hasil[0].kadar_beli, //harga%
          kode_jenis: hasil[0].kode_jenis, //kodebarang
          kode_barcode: "-",
          p: parseFloat(data.p),
          disc: data.discGlobalGr,
          pkg: parseFloat(data.pkg),
          bruto: parseFloat(data.bruto).toFixed(3), //berat
        };
        var simpan_level2 = {
          netto_disc:
            (parseFloat(data.bruto) * parseFloat(res.data.level2)) / 100 -
            (parseFloat(data.discGlobalPr) * parseFloat(data.bruto)) / 100,
          gross: parseFloat(data.bruto) + parseFloat(data.pkg), //berat + bungkus
          netto:
            (parseFloat(data.bruto) * parseFloat(res.data.level2)) / 100 || 0, //murni
          qty: 1,
          k: parseFloat(data.k),
          ongkos_rp: parseFloat(data.rp === undefined ? 0 : data.rp), //ongkos
          kadar_cetak: hasil[0].kadar_cetak,
          disc_persen: data.discGlobalPr,
          kadar_jual: parseFloat(data.kadarTransaksi), //harga%
          kadar_level2: res.data.level2,
          kadar_level3: res.data.level3,
          kadar_modal: hasil[0].kadar_beli, //harga%
          kode_jenis: hasil[0].kode_jenis, //kodebarang
          kode_barcode: "-",
          p: parseFloat(data.p),
          disc: (parseFloat(data.discGlobalPr) / 100) * parseFloat(data.bruto),
          pkg: parseFloat(data.pkg),
          bruto: parseFloat(data.bruto), //berat
        };
        var simpan_level3 = {
          netto_disc:
            (parseFloat(data.bruto) * parseFloat(res.data.level3)) / 100 -
            (parseFloat(data.discGlobalPr) * parseFloat(data.bruto)) / 100,
          gross: parseFloat(data.bruto) + parseFloat(data.pkg), //berat + bungkus
          netto:
            (parseFloat(data.bruto) * parseFloat(res.data.level3)) / 100 || 0, //murni
          qty: 1,
          k: parseFloat(data.k),
          ongkos_rp: parseFloat(data.rp === undefined ? 0 : data.rp), //ongkos
          kadar_cetak: hasil[0].kadar_cetak,
          disc_persen: data.discGlobalPr,
          kadar_jual: parseFloat(data.kadarTransaksi), //harga%
          kadar_level2: res.data.level2,
          kadar_level3: res.data.level3,
          kadar_modal: hasil[0].kadar_beli, //harga%
          kode_jenis: hasil[0].kode_jenis, //kodebarang
          kode_barcode: "-",
          p: parseFloat(data.p),
          disc: (parseFloat(data.discGlobalPr) / 100) * parseFloat(data.bruto),
          pkg: parseFloat(data.pkg),
          bruto: parseFloat(data.bruto), //berat
        };
        var array =
          JSON.parse(localStorage.getItem("transaksiPenjualan")) || [];
        var array_level2 =
          JSON.parse(localStorage.getItem("transaksiPenjualan_level2")) || [];
        var array_level3 =
          JSON.parse(localStorage.getItem("transaksiPenjualan_level3")) || [];
        array.push(simpan);
        array_level2.push(simpan_level2);
        array_level3.push(simpan_level3);
        var GrandTotalNetto = array
          .map((list) => parseFloat(list.netto_disc))
          .reduce((a, b) => a + b, 0);
        localStorage.setItem("GrandTotalNetto", GrandTotalNetto);
        localStorage.setItem("transaksiPenjualan", JSON.stringify(array));
        localStorage.setItem(
          "transaksiPenjualan_level2",
          JSON.stringify(array_level2)
        );
        localStorage.setItem(
          "transaksiPenjualan_level3",
          JSON.stringify(array_level3)
        );
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Data Berhasil Ditambahkan!",
          showConfirmButton: false,
          timer: 1500,
        });
        this.props.dispatch(getListTransaksiPenjualan());
        this.props.dispatch(getTotalNW());
        this.props.dispatch(reset("FormTransaksiPenjualan"));

        this.props.dispatch(getNetto("transaksiPenjualan"));
        this.setState({
          discGlobalGr:
            (parseFloat(0) / 100) *
            parseFloat(localStorage.getItem("GrandTotalNetto")),
          discGlobalPr: 0,
          GrandTotalNetto: (
            parseFloat(localStorage.getItem("GrandTotalNetto")) - 0
          ).toFixed(3),
        });
      })
      .catch((err) =>
        gagal("Kode Kelompok Belum Disesuaikan, Mohon Sesuaikan Dahulu")
      );
  }

  transaksiPenjualanSubmit() {
    if (this.props.dataPO === []) {
      if (localStorage.getItem("transaksiPenjualan") === null) {
        warning("Barang masih kosong, Silahkan tambah barang");
        return false;
      } else if (this.state.noBon === "") {
        warning("Mohon isi Nomor bon");
        return false;
      } else if (this.state.lokasi === "") {
        warning("Mohon pilih lokasi");
        return false;
      } else if (this.state.sales === "") {
        warning("Mohon pilih Sales");
        return false;
      } else if (this.state.namaToko === "") {
        warning("Mohon pilih toko");
        return false;
      } else if (this.state.discGlobalPr === "") {
        warning("Mohon isi Discount");
        return false;
      } else if (this.state.discGlobalGr === "") {
        warning("Mohon isi Discount Gr");
        return false;
      } else if (this.state.totalNWExt === 0) {
        warning("Mohon isi Grand Total");
        return false;
      } else if (this.state.tanggal === "") {
        warning("Mohon isi Tanggal");
        return false;
      }
    }
    var simpan = {};
    switch (this.state.level_customer) {
      case "LEVEL1":
        simpan = {
          no_bon: this.state.noBon.toUpperCase(),
          no_penjualan: this.props.noFaktur.toUpperCase(),
          no_po: localStorage.getItem("no_po") || "-",
          no_packing: localStorage.getItem("packing_nomor") || "-",
          tanggal: this.state.tanggal,
          kode_lokasi: this.state.lokasi.toUpperCase(),
          kode_sales: this.state.sales.toUpperCase(),
          kode_customer: this.state.kodeCustomer.toUpperCase(),
          level_customer_trx: this.state.level_customer,
          kode_customer_level2: this.state.kode_customer_level1,
          kode_customer_level3: this.state.kode_customer_level2,
          detail_barang: JSON.parse(localStorage.getItem("transaksiPenjualan")),
          total_netto: this.props.totalNW,
          disc_persen: this.state.discGlobalPr,
          disc: this.state.discGlobalGr,
          total_netto_disc: this.state.GrandTotalNetto,
          total_netto_disc_ext: this.state.totalNWExt,
        };
        break;
      case "LEVEL2":
        simpan = {
          no_bon: this.state.noBon.toUpperCase(),
          no_penjualan: this.props.noFaktur.toUpperCase(),
          no_po: localStorage.getItem("no_po") || "-",
          no_packing: localStorage.getItem("packing_nomor") || "-",
          tanggal: this.state.tanggal,
          kode_lokasi: this.state.lokasi.toUpperCase(),
          kode_sales: this.state.sales.toUpperCase(),
          kode_customer: this.state.kode_customer_level1,
          level_customer_trx: this.state.level_customer,
          kode_customer_level2: this.state.kodeCustomer,
          kode_customer_level3: this.state.kode_customer_level2,
          detail_barang: JSON.parse(localStorage.getItem("transaksiPenjualan")),
          total_netto: this.props.totalNW,
          disc_persen: this.state.discGlobalPr,
          disc: this.state.discGlobalGr,
          total_netto_disc: this.state.GrandTotalNetto,
          total_netto_disc_ext: this.state.totalNWExt,
        };
        break;
      case "LEVEL3":
        simpan = {
          no_bon: this.state.noBon.toUpperCase(),
          no_penjualan: this.props.noFaktur.toUpperCase(),
          no_po: localStorage.getItem("no_po") || "-",
          no_packing: localStorage.getItem("packing_nomor") || "-",
          tanggal: this.state.tanggal,
          kode_lokasi: this.state.lokasi.toUpperCase(),
          kode_sales: this.state.sales.toUpperCase(),
          kode_customer: this.state.kode_customer_level1,
          level_customer_trx: this.state.level_customer,
          kode_customer_level2: this.state.kode_customer_level2,
          kode_customer_level3: this.state.kodeCustomer,
          detail_barang: JSON.parse(localStorage.getItem("transaksiPenjualan")),
          total_netto: this.props.totalNW,
          disc_persen: this.state.discGlobalPr,
          disc: this.state.discGlobalGr,
          total_netto_disc: this.state.GrandTotalNetto,
          total_netto_disc_ext: this.state.totalNWExt,
        };
        break;
      default:
        warning("Ada Kesalah Saat Proses Level Customer");
        break;
    }
    this.props.dispatch(progressSend());
    AxiosMasterPost("penjualan/simpan", simpan)
      .then(
        () =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Ditambahkan!",
            showConfirmButton: false,
            timer: 1500,
          })
            .then(() => {
              switch (this.state.level_customer) {
                case "LEVEL1":
                  this.printNota("LEVEL1");
                  break;
                case "LEVEL2":
                  this.printNota("LEVEL1");
                  this.printNota("LEVEL2");
                  break;
                case "LEVEL3":
                  this.printNota("LEVEL1");
                  this.printNota("LEVEL2");
                  this.printNota("LEVEL3");
                  break;
                default:
                  break;
              }
            })
            .then(() => this.props.dispatch(finishSend()))
            .then(() => localStorage.removeItem("transaksiPenjualan"))
            .then(() => localStorage.removeItem("transaksiPenjualan_level2"))
            .then(() => localStorage.removeItem("transaksiPenjualan_level3"))
            .then(() => localStorage.removeItem("GrandTotalNetto"))
            .then(() => localStorage.removeItem("noFaktur"))
            .then(() => localStorage.removeItem("no_po"))
            .then(() => this.props.dispatch(getUUID()))
            .then(() => this.props.dispatch(getTotalNW()))
            .then(() => this.props.dispatch(getListTransaksiPenjualan()))
            .then(() =>
              this.setState({
                noBon: "",
                namaCustomer: "",
                lokasi: "",
                sales: "",
                namaToko: "",
                tanggal: "",
                toko: "",
                GrandTotalNetto: 0,
              })
            )
            .then(() => document.getElementById("myForm").reset())
        // .then(() => window.location.reload())
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: `${err}`,
          showConfirmButton: false,
          timer: 1500,
        }).then(() => this.props.dispatch(finishSend()))
      );
  }

  printNota(level) {
    console.log("PRINT INI DI PANGGIL");
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = [];
    var disc = [];
    var footer = [];
    var columTable = [];
    switch (level) {
      case "LEVEL1":
        table = JSON.parse(localStorage.getItem("transaksiPenjualan"));
        // for each ticket pass all its data into an array
        table.forEach((ticket, i) => {
          const ticketData = [
            ++i,
            ticket.kadar_cetak,
            ticket.kode_jenis,
            parseFloat(ticket.bruto).toFixed(3),
            ticket.kadar_jual,
            parseFloat(ticket.disc).toFixed(3),
            parseFloat(ticket.netto_disc).toFixed(3),
            ticket.pkg,
            ticket.qty,
            parseFloat(ticket.ongkos_rp).toLocaleString("id-ID"),
            "",
          ];
          // push each tickcet's info into a row
          tableRows.push(ticketData);
        });
        disc = [
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "Disc Global",
          this.state.discGlobalGr,
        ];
        tableRows.push(disc);
        footer = [
          "",
          "",
          "",
          table
            .map((list) => parseFloat(list.bruto))
            .reduce((a, b) => a + b, 0)
            .toFixed(3),
          "",
          table
            .map((list) => parseFloat(list.disc))
            .reduce((a, b) => a + b, 0)
            .toFixed(3),
          table
            .map((list) => parseFloat(list.netto))
            .reduce((a, b) => a + b, 0)
            .toFixed(3),
          table.map((list) => parseFloat(list.pkg)).reduce((a, b) => a + b, 0),
          table.map((list) => parseFloat(list.qty)).reduce((a, b) => a + b, 0),
          table
            .map((list) => parseFloat(list.ongkos_rp))
            .reduce((a, b) => a + b, 0)
            .toLocaleString("id-ID"),
          this.state.totalNWExt,
        ];
        tableRows.push(footer);
        console.log(JSON.stringify(tableRows));
        columTable = [
          "No",
          "Kadar",
          "Kode",
          "Bruto \n(Gr)",
          "Harga \n(%)",
          "Diskon",
          "Netto \n(Gr)",
          "Pkg",
          "Qty",
          "Ongkos \n(Rp)",
          "Grand Total\nNetto Ext",
        ];
        // AKHIR INISIALISASI AUTOTABLE
        PrintNota(
          "no Trx",
          this.props.noFaktur,
          "Tanggal",
          this.state.tanggal,
          "No Bon",
          this.state.noBon,
          "Sales",
          this.state.sales,
          "ADMIN",
          this.state.tanggal,
          "",
          columTable,
          "Transaksi Penjualan",
          tableRows
        );
        break;
      case "LEVEL2":
        table = JSON.parse(localStorage.getItem("transaksiPenjualan_level2"));
        // for each ticket pass all its data into an array
        table.forEach((ticket, i) => {
          const ticketData = [
            ++i,
            ticket.kadar_cetak,
            ticket.kode_jenis,
            parseFloat(ticket.bruto).toFixed(3),
            ticket.kadar_level2,
            parseFloat(ticket.disc).toFixed(3),
            parseFloat(ticket.netto_disc).toFixed(3),
            ticket.pkg,
            ticket.qty,
            parseFloat(ticket.ongkos_rp).toLocaleString("id-ID"),
            "",
          ];
          // push each tickcet's info into a row
          tableRows.push(ticketData);
        });
        disc = [
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "Disc Global",
          this.state.discGlobalGr,
        ];
        tableRows.push(disc);
        footer = [
          "",
          "",
          "",
          table
            .map((list) => parseFloat(list.bruto))
            .reduce((a, b) => a + b, 0)
            .toFixed(3),
          "",
          table
            .map((list) => parseFloat(list.disc))
            .reduce((a, b) => a + b, 0)
            .toFixed(3),
          table
            .map((list) => parseFloat(list.netto))
            .reduce((a, b) => a + b, 0)
            .toFixed(3),
          table.map((list) => parseFloat(list.pkg)).reduce((a, b) => a + b, 0),
          table.map((list) => parseFloat(list.qty)).reduce((a, b) => a + b, 0),
          table
            .map((list) => parseFloat(list.ongkos_rp))
            .reduce((a, b) => a + b, 0)
            .toLocaleString("id-ID"),
          table
            .map((list) => parseFloat(list.netto))
            .reduce((a, b) => a + b, 0) - parseFloat(this.state.discGlobalGr),
        ];
        tableRows.push(footer);
        console.log(JSON.stringify(tableRows));
        columTable = [
          "No",
          "Kadar",
          "Kode",
          "Bruto \n(Gr)",
          "Harga \n(%)",
          "Diskon",
          "Netto \n(Gr)",
          "Pkg",
          "Qty",
          "Ongkos \n(Rp)",
          "Grand Total\nNetto Ext",
        ];
        // AKHIR INISIALISASI AUTOTABLE
        PrintNota(
          "no Trx",
          this.props.noFaktur,
          "Tanggal",
          this.state.tanggal,
          "No Bon",
          this.state.noBon,
          "Sales",
          this.state.sales,
          "ADMIN",
          this.state.tanggal,
          "",
          columTable,
          "Transaksi Penjualan",
          tableRows
        );
        break;
      case "LEVEL3":
        table = JSON.parse(localStorage.getItem("transaksiPenjualan_level3"));
        // for each ticket pass all its data into an array
        table.forEach((ticket, i) => {
          const ticketData = [
            ++i,
            ticket.kadar_cetak,
            ticket.kode_jenis,
            parseFloat(ticket.bruto).toFixed(3),
            ticket.kadar_level3,
            parseFloat(ticket.disc).toFixed(3),
            parseFloat(ticket.netto_disc).toFixed(3),
            ticket.pkg,
            ticket.qty,
            parseFloat(ticket.ongkos_rp).toLocaleString("id-ID"),
            "",
          ];
          // push each tickcet's info into a row
          tableRows.push(ticketData);
        });
        disc = [
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "Disc Global",
          this.state.discGlobalGr,
        ];
        tableRows.push(disc);
        footer = [
          "",
          "",
          "",
          table
            .map((list) => parseFloat(list.bruto))
            .reduce((a, b) => a + b, 0)
            .toFixed(3),
          "",
          table
            .map((list) => parseFloat(list.disc))
            .reduce((a, b) => a + b, 0)
            .toFixed(3),
          table
            .map((list) => parseFloat(list.netto))
            .reduce((a, b) => a + b, 0)
            .toFixed(3),
          table.map((list) => parseFloat(list.pkg)).reduce((a, b) => a + b, 0),
          table.map((list) => parseFloat(list.qty)).reduce((a, b) => a + b, 0),
          table
            .map((list) => parseFloat(list.ongkos_rp))
            .reduce((a, b) => a + b, 0)
            .toLocaleString("id-ID"),
          this.state.totalNWExt,
        ];
        tableRows.push(footer);
        console.log(JSON.stringify(tableRows));
        columTable = [
          "No",
          "Kadar",
          "Kode",
          "Bruto \n(Gr)",
          "Harga \n(%)",
          "Diskon",
          "Netto \n(Gr)",
          "Pkg",
          "Qty",
          "Ongkos \n(Rp)",
          "Grand Total\nNetto Ext",
        ];
        // AKHIR INISIALISASI AUTOTABLE
        PrintNota(
          "no Trx",
          this.props.noFaktur,
          "Tanggal",
          this.state.tanggal,
          "No Bon",
          this.state.noBon,
          "Sales",
          this.state.sales,
          "ADMIN",
          this.state.tanggal,
          "",
          columTable,
          "Transaksi Penjualan",
          tableRows
        );
        break;
      default:
        warning("Error Print");
        break;
    }
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  inputGrandTotal = (input) => (e) => {
    var GrandNetto = this.props.sumGross - parseFloat(this.state.discGlobalGr);
    var grandTotal = e.target.value;
    console.log(GrandNetto + "-" + grandTotal);
    if (GrandNetto - grandTotal < -1) {
      warning("Selisih lebih dari 1");
      e.target.value = "";
      return false;
    } else {
      if (GrandNetto - grandTotal <= 1) {
        this.setState({
          [input]: e.target.value,
        });
      } else {
        warning("Selisih lebih dari 1");
        e.target.value = "";
        return false;
      }
    }
  };
  discount = (input) => (e) => {
    var hasil =
      (parseFloat(e.target.value) / 100) *
      parseFloat(localStorage.getItem("GrandTotalNetto"));
    this.setState({
      discGlobalGr: hasil.toFixed(3),
      discGlobalPr: e.target.value,
      GrandTotalNetto: (
        parseFloat(localStorage.getItem("GrandTotalNetto")) - hasil.toFixed(3)
      ).toFixed(3),
    });
  };
  onSubmit(data, input) {
    this.setState({
      [input]: data,
    });
  }
  onSubmitToko(data) {
    let param = data;
    let hasil = this.props.listCustomer.filter(
      (data) => data.kode_customer === param
    );
    this.setState({
      kodeCustomer: data,
      level_customer: hasil[0].level_customer,
    });
    this.props.change("level_customer", hasil[0].level_customer);
    AxiosMasterGet(`customer/get/by-kode-customer/${data}`).then((res) => {
      let levelnya = res.data;
      this.setState({
        kode_customer_level1: levelnya.kode_customer_level1,
        kode_customer_level2: levelnya.kode_customer_level2,
      });
      this.props.change("level_customer_1", levelnya.kode_customer_level1);
      this.props.change("level_customer_2", levelnya.kode_customer_level2);
    });
  }

  render() {
    return (
      <div className="container-fluid">
        {String(this.props.listPacking) !== "" ? (
          <div className="col-lg-12">
            <div className="card">
              <HeaderCard title="Transaksi Penjualan" />
              <div className="card-body">
                <div className="row">
                  <div className="col-lg-12">
                    <form
                      id="myForm"
                      onKeyDown={(event) => {
                        return event.key !== "Enter";
                      }}
                    >
                      <div className="row">
                        <div className="col-lg-4 col-md-4">
                          <div className="form-group">
                            <label htmlFor="">Tanggal</label>
                            <input
                              name="tanggal"
                              type="date"
                              label="Tanggal"
                              className="form-control"
                              onChange={this.inputChange("tanggal")}
                              value={this.state.tanggal}
                            />
                          </div>
                        </div>
                        <div className="col-lg-4 col-md-4">
                          <div className="form-group">
                            <label htmlFor="">No Bon</label>
                            <input
                              name="noBon"
                              type="text"
                              label="No Bon"
                              className="form-control"
                              onChange={this.inputChange("noBon")}
                              value={this.state.noBon}
                            />
                          </div>
                        </div>
                        <div className="col-lg-4 col-md-4"></div>
                        <div className="col-lg-4 col-md-4">
                          <div className="form-group">
                            <label htmlFor="">Lokasi</label>
                            <SelectSearch
                              options={[
                                {
                                  value: "SALES",
                                  name: "SALES",
                                },
                                {
                                  value: "PUSAT",
                                  name: "PUSAT",
                                },
                              ]}
                              search
                              placeholder="Silahkan Pilih"
                              onChange={(data) => this.onSubmit(data, "lokasi")}
                              value={this.state.lokasi}
                            />
                          </div>
                        </div>
                        <div className="col-lg-4 col-md-4">
                          <div className="form-group">
                            <label htmlFor="">Sales</label>
                            <SelectSearch
                              options={this.props.listSales.map((obj) => {
                                var payload = {
                                  value: obj.kode_sales,
                                  name: obj.nama_sales,
                                };
                                return payload;
                              })}
                              search
                              placeholder="Silahkan Pilih"
                              onChange={(data) => this.onSubmit(data, "sales")}
                              value={this.state.sales}
                            />
                          </div>
                        </div>
                        <div className="col-lg-4 col-md-4">
                          <div className="form-group">
                            <label htmlFor="">Nama Toko</label>
                            <SelectSearch
                              options={this.props.listCustomer.map((obj) => {
                                var payload = {
                                  value: obj.kode_customer,
                                  name:
                                    obj.nama_toko + " - " + obj.nama_customer,
                                };
                                return payload;
                              })}
                              search
                              placeholder="Silahkan Pilih"
                              onChange={(data) => this.onSubmitToko(data)}
                              onBlur={(data) => this.onSubmit(data, "toko")}
                              value={this.state.toko}
                            />
                          </div>
                        </div>

                        <div className="col-lg-4">
                          <Field
                            name="level_customer"
                            component={renderField}
                            type="text"
                            label="Level Customer"
                            placeholder="Masukan Level Customer"
                            readOnly
                          />
                        </div>
                        <div className="col-lg-4">
                          <Field
                            name="level_customer_1"
                            component={renderFieldSelect}
                            options={this.props.listCustomer.map((obj) => {
                              var payload = {
                                value: obj.kode_customer,
                                name: obj.nama_toko + " - " + obj.nama_customer,
                              };
                              return payload;
                            })}
                            type="text"
                            label="Level Customer 1"
                            placeholder="Level Customer 1"
                            disabled
                          />
                        </div>
                        <div className="col-lg-4">
                          <Field
                            name="level_customer_2"
                            component={renderFieldSelect}
                            options={this.props.listCustomer.map((obj) => {
                              var payload = {
                                value: obj.kode_customer,
                                name: obj.nama_toko + " - " + obj.nama_customer,
                              };
                              return payload;
                            })}
                            type="text"
                            label="Level Customer 2"
                            placeholder="Level Customer 2"
                            readOnly
                            disabled
                          />
                        </div>

                        <div className="col-lg-3 mb-3">
                          <label htmlFor="">Total NW</label>
                          <input
                            name="totalNW"
                            type="number"
                            className="form-control"
                            min={0}
                            step={0.01}
                            label="totalNW"
                            value={this.props.totalNW}
                            readOnly={true}
                          />
                        </div>
                        <div className="col-lg-3">
                          <label htmlFor="">Disc Global (%)</label>
                          <input
                            name="discGlobal"
                            type="number"
                            className="form-control"
                            min={0}
                            step={0.001}
                            label="discGlobal"
                            onChange={this.discount("discGlobalPr")}
                            value={this.state.discGlobalPr}
                          />
                        </div>
                        <div className="col-lg-3">
                          <label htmlFor="">Disc (Gr)</label>
                          <input
                            name="discGlobal"
                            type="number"
                            className="form-control"
                            label="discGlobal"
                            value={this.state.discGlobalGr}
                            readOnly={true}
                          />
                        </div>
                        <div className="col-lg-3">
                          <label htmlFor="">Grand Total Netto</label>
                          <input
                            name="totalNW"
                            type="number"
                            className="form-control"
                            label="totalNW"
                            value={this.state.GrandTotalNetto}
                            readOnly={true}
                          />
                        </div>
                        <div className="col-lg-9"></div>
                        <div className="col-lg-3 mb-3">
                          <label htmlFor="">Grand Total Netto Ext</label>
                          <input
                            name="totalNWExt"
                            type="text"
                            className="form-control"
                            label="totalNWExt"
                            onChange={this.inputChange("totalNWExt")}
                            // onBlur={this.inputGrandTotal("totalNWExt")}
                          />
                        </div>
                      </div>
                    </form>
                  </div>
                  <div className="col-lg-12 col-md-12 mt-3">
                    <div className="row">
                      <div className="col-lg-2"></div>
                      <div className="col-lg-6 col-md-7 mb-3">
                        <div className="row">
                          {this.state.kodeCustomer !== "" ? (
                            <button
                              className="btn btn-primary"
                              data-toggle="modal"
                              type="button"
                              data-target="#barcodeModal"
                            >
                              BARCODE (+)
                            </button>
                          ) : (
                            <button
                              className="btn btn-primary"
                              data-toggle="modal"
                              type="button"
                              data-target="#barcodeModal"
                              disabled
                            >
                              BARCODE (+)
                            </button>
                          )}
                          {this.state.kodeCustomer !== "" ? (
                            <button
                              className="btn btn-primary ml-2"
                              data-toggle="modal"
                              data-target="#timbanganModal"
                            >
                              TIMBANGAN (+)
                            </button>
                          ) : (
                            <button
                              className="btn btn-primary ml-2"
                              data-toggle="modal"
                              data-target="#timbanganModal"
                              disabled
                            >
                              TIMBANGAN (+)
                            </button>
                          )}
                          <button
                            className="btn btn-primary ml-2"
                            data-toggle="modal"
                            data-target="#preorderModal"
                          >
                            PRE ORDER (+)
                          </button>
                          <button
                            className="btn btn-primary ml-2"
                            data-toggle="modal"
                            data-target="#packingModal"
                          >
                            PACKING (+)
                          </button>
                        </div>
                      </div>
                      <div className="col-lg-4">
                        <div className="text-right">
                          <button
                            className="btn btn-primary"
                            onClick={() => this.props.dispatch(showModal())}
                          >
                            Tambah Barang
                            <i class="fa fa-plus ml-3" aria-hidden="true"></i>
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="col-lg-12 mt-5">
                    <GlobalTabel
                      keyField="id"
                      data={this.props.transaksiPenjualan}
                      columns={this.state.columns}
                    />
                    <div className="text-right">
                      <button
                        className="btn btn-primary"
                        disabled={this.props.onSend}
                        onClick={() => this.transaksiPenjualanSubmit()}
                      >
                        {this.props.onSend ? (
                          <span
                            className="spinner-border spinner-border-sm mr-3"
                            aria-hidden="true"
                          ></span>
                        ) : null}
                        Simpan
                        <i
                          class="fa fa-paper-plane ml-3"
                          aria-hidden="true"
                        ></i>
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        ) : (
          <Skeleton width="100%" height="100%" />
        )}
        <div
          id="barcodeModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-xl">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Data Barcode
                </h4>
                <button
                  type="button"
                  id="dismissModal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <FormBarcodePenjualanCustomer
                  kodeCustomer={this.state.kodeCustomer}
                />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
        <div
          id="timbanganModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-xl">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Data Timbangan
                </h4>
                <button
                  type="button"
                  id="dismissModal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <FormTimbanganPenjualanCustomer
                  kodeCustomer={this.state.kodeCustomer}
                />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
        <div
          id="preorderModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-xl">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Data PO
                </h4>
                <button
                  type="button"
                  id="dismissModal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <FormPOCustomer kodeCustomer={this.state.kodeCustomer} />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
        <div
          id="packingModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-xl">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Data Packing
                </h4>
                <button
                  type="button"
                  id="dismissModal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <FormPackingCustomer kodeCustomer={this.state.kodeCustomer} />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
        <ModalMasterData
          content={
            <Suspense
              fallback={<Skeleton width={"100%"} height={50} count={4} />}
            >
              <FormTransaksiPenjualan
                onSubmit={(data) => this.handleSubmit(data)}
              />
            </Suspense>
          }
          title="Tambah Data"
        />
      </div>
    );
  }
}

TransaksiPenjualan = reduxForm({
  form: "TransaksiPenjualan",
  enableReinitialize: true,
})(TransaksiPenjualan);
export default connect(maptostate, null)(TransaksiPenjualan);
